<template>
  <div class="CommercialOpportunity">
    <div
      class="CommercialOpportunityBox"
      v-for="(item, index) in dataFormList"
      :key="index"
    >
      <div class="CommercialOpportunityBoxHeader" @click="clickStatus(index)">
        <img
          v-if="item.CustPictureUrl"
          class="headerImg"
          draggable="false"
          :src="item.CustPictureUrl"
          alt=""
        />
        <img
          v-if="!item.CustPictureUrl"
          class="headerImg"
          draggable="false"
          src="@/assets/image/InstallHeadPortrait.png"
          alt=""
        />
        <div>
          <p>{{ item.CustName }}</p>
          <span>{{ item.CustDemandProduct }}</span>
          <em>{{ item.ModeltypeNo ? item.ModeltypeNo : "無指定型號" }}</em>
        </div>
        <!-- <img
          @click="clickStatus(true, index)"
          v-if="!statusList[index]"
          class="CustomerDataHeaderImgRight"
          draggable="false"
          src="@/assets/image/right.png"
          alt=""
        />
        <img
          @click="clickStatus(false, index)"
          v-if="statusList[index]"
          class="CustomerDataHeaderImgBottom"
          draggable="false"
          src="@/assets/image/bottom.png"
          alt=""
        /> -->
      </div>
      <div class="CommercialOpportunityBoxContent" v-if="statusList[index]">
        <h2>地址：{{ item.CustFullAddress }}</h2>
        <h2>電話：{{ item.CustMobile }}</h2>
        <!-- <h2>
          產品型號：{{ item.ModeltypeNo ? item.ModeltypeNo : "無指定型號" }}
        </h2> -->
        <h3 style="margin-top: 4px">通報日期：{{ item.OpDate }}</h3>
        <h3>備註：{{ item.Memo1 }}</h3>
        <!-- <div @click="showAmends(item)">受理</div> -->
      </div>
    </div>
    <!-- 完成弹窗 -->
    <!-- 完成弹窗 -->
    <!-- 完成弹窗 -->
    <van-popup v-model="show" class="ReservationServicePopupSuccess">
      <img draggable="false" src="@/assets/image/success.png" alt="" />
      <h2>完成</h2>
      <div @click="hidePopup()">返回商機通報列表</div>
    </van-popup>
    <!-- 受理弹窗 -->
    <!-- 受理弹窗 -->
    <!-- 受理弹窗 -->
    <van-popup v-model="showAmend" class="ReservationServicePopupAmend">
      <h2>請選擇</h2>
      <van-radio-group v-model="radioStockUp">
        <van-radio name="true">購買</van-radio>
      </van-radio-group>
      <div class="ReservationServicePopupAmendFrame">
        <select
          v-model="selectType"
          placeholder="xiala"
          :style="ProductCarouselImg"
        >
          <option value="0" hidden>請選擇機型</option>
          <option
            v-for="(item, index) in NewWaterProductList"
            :key="index"
            :value="item.ModelTypeId"
            >{{ item.ModelTypeName }}</option
          >
        </select>
        <van-field v-model="digit" type="digit" label="數量" />
      </div>
      <van-radio-group v-model="radioStockUp">
        <van-radio name="false">未購買，原因</van-radio>
      </van-radio-group>
      <textarea placeholder="請填寫原因" v-model="PurchasedRemark"></textarea>
      <div class="ReservationServicePopupAmendButtom">
        <div
          class="ReservationServicePopupAmendButtomLeft"
          @click="showAmend = false"
        >
          取消
        </div>
        <div
          class="ReservationServicePopupAmendButtomRight"
          @click="hideAmends()"
        >
          送出
        </div>
      </div>
    </van-popup>
    <!-- 空组件 -->
    <noContent v-if="dataFormListStatus" />
    <!-- 加载中组件 -->
    <Loading :show="loadingShow" />
  </div>
</template>

<script>
import noContent from "@/components/noContent";
import Loading from "@/components/Loading";
import {
  POST_GetWaterOpportunity,
  GET_GetNewWaterProduct,
  POST_FillBackOpportunity
} from "@/utils/api";
export default {
  name: "CommercialOpportunity",
  inject: ["reload"],
  components: {
    noContent,
    Loading
  },
  data() {
    return {
      loadingShow: false,
      dataFormListStatus: false,
      itemList: "",
      PurchasedRemark: "",
      NewWaterProductList: [],
      ProductCarouselImg: {
        backgroundImage: "url(" + require("@/assets/image/bottom.png") + ")",
        backgroundRepeat: "no-repeat",
        backgroundSize: "11px 6px",
        backgroundPosition: "80px 15px"
      },
      digit: "",
      selectType: "0",
      type: "",
      radioStockUp: "true",
      showAmend: false,
      show: false,
      dataFormList: [
        // {
        //   id: "111",
        //   headerImgUrl: require("@/assets/image/InstallHeadPortrait.png"),
        //   name: "王阿英",
        //   type: "淨水器",
        //   location: "台中市西區敦化南路1號5樓",
        //   phone: "0923452345",
        //   data: "2020年02月14日 14:00",
        //   remark: "為年長獨居女性，對水的要求高",
        //   status: "0"
        // },
        // {
        //   id: "222",
        //   headerImgUrl: require("@/assets/image/InstallHeadPortrait.png"),
        //   name: "陳大可",
        //   type: "淨水器濾心",
        //   location: "台中市西區敦化南路1號5樓",
        //   phone: "0923452345",
        //   data: "2020年02月14日 14:00",
        //   remark: "為年長獨居女性，對水的要求高",
        //   status: "1"
        // }
      ],
      statusList: []
    };
  },
  created() {
    this.GetWaterOpportunity();
    this.GetCustomerRemark();
  },
  methods: {
    // 获取商机通报列表
    GetWaterOpportunity() {
      this.loadingShow = true;
      var UofIds = {
        UofId: sessionStorage.getItem("DistributionUofId")
      };
      POST_GetWaterOpportunity(UofIds).then(response => {
        this.loadingShow = false;
        if (response.State) {
          // 判断是否从搜索页过来
          if (this.$route.query.CustPhone) {
            for (var d = 0; d < response.Data.length; d++) {
              if (response.Data[d].CustMobile == this.$route.query.CustPhone) {
                this.dataFormList.push(response.Data[d]);
                this.statusList.push(false);
              }
            }
          } else {
            // 判断不是搜索页显示全部
            this.dataFormList = response.Data;
            for (var i = 0; i < response.Data.length; i++) {
              this.statusList.push(false);
            }
          }
          this.dataFormListStatus = false;
        } else {
          this.dataFormListStatus = true;
        }
        // 判断列表没有数据是显示空组件
        if (this.dataFormList.length == 0) {
          this.dataFormListStatus = true;
        } else {
          this.dataFormListStatus = false;
        }
      });
    },
    // 获取型号列表
    GetCustomerRemark() {
      GET_GetNewWaterProduct().then(response => {
        this.NewWaterProductList = response.Data;
        this.selectValue = response.Data[0];
      });
    },
    // 关闭完成弹窗
    hidePopup() {
      this.show = false;
      this.reload();
    },
    // 打开完成弹窗
    showAmends(item) {
      this.showAmend = true;
      this.itemList = item;
      this.PurchasedRemark = item.UnPurchaseReason;
      this.selectType = item.ModelTypeId;
      this.digit = item.Qty;
      if (item.UnPurchaseReason) {
        this.radioStockUp = "false";
      } else {
        this.radioStockUp = "true";
      }
    },
    // 关闭完成弹窗
    hideAmends() {
      var IsPurchase = "";
      var NotPurchasedReason = "";
      var PurchasedRemark = "";
      if (this.radioStockUp == "true") {
        IsPurchase = true;
        NotPurchasedReason = "";
        PurchasedRemark = this.PurchasedRemark;
      } else {
        IsPurchase = false;
        NotPurchasedReason = this.PurchasedRemark;
        PurchasedRemark = "";
      }
      var datas = {
        FillBackId: this.itemList.Fillbackid,
        OpportunityId: this.itemList.Opportiunityid,
        ModelTypeId: this.selectType !== "0" ? this.selectType : "",
        IsPurchase: IsPurchase,
        NotPurchasedReason: NotPurchasedReason,
        PurchasedRemark: PurchasedRemark,
        Qty: this.digit,
        Status: "979640001"
      };
      POST_FillBackOpportunity(datas).then(response => {
        this.showAmend = false;
        if (response.State) {
          this.show = true;
        }
      });
    },
    clickStatus(index) {
      this.statusList.splice(index, 1, !this.statusList[index]);
    }
  }
};
</script>

<style lang="scss">
.CommercialOpportunity {
  padding-right: 5.33%;
  padding-top: 24px;
  .CommercialOpportunityBox {
    padding: 20px 5.37%;
    background: rgba(255, 255, 255, 1);
    border-radius: 8px;
    margin-bottom: 10px;
    .CommercialOpportunityBoxHeader {
      display: flex;
      align-items: center;
      .headerImg {
        border-radius: 50%;
        width: 30px;
        height: 30px;
        margin-right: 6px;
      }
      div {
        p {
          font-size: 16px;
          font-weight: 500;
          color: rgba(61, 61, 61, 1);
          line-height: 22px;
        }
        span {
          font-size: 12px;
          font-weight: 500;
          color: rgba(189, 189, 189, 1);
          line-height: 17px;
        }
        em {
          font-size: 12px;
          font-weight: 500;
          color: rgba(189, 189, 189, 1);
          line-height: 17px;
          margin-left: 5px;
        }
      }
      .CustomerDataHeaderImgRight {
        width: 9px;
        height: 16px;
        margin-left: auto;
      }
      .CustomerDataHeaderImgBottom {
        width: 15px;
        height: 8px;
        margin-left: auto;
      }
    }
    .CommercialOpportunityBoxContent {
      margin-top: 12px;
      h2 {
        font-size: 14px;
        font-weight: 500;
        color: rgba(61, 61, 61, 1);
        line-height: 20px;
      }
      h3 {
        font-size: 12px;
        font-weight: 500;
        color: rgba(189, 189, 189, 1);
        line-height: 17px;
      }
      div {
        margin-top: 10px;
        margin-left: auto;
        text-align: center;
        background: linear-gradient(
          180deg,
          rgba(28, 118, 254, 1) 0%,
          rgba(28, 118, 254, 1) 100%
        );
        box-shadow: 0px 3px 12px 0px rgba(28, 118, 254, 0.3);
        border-radius: 6px;
        font-size: 14px;
        font-weight: 500;
        color: rgba(255, 255, 255, 1);
        line-height: 30px;
        width: 66px;
        height: 30px;
      }
    }
  }
  .ReservationServicePopupSuccess {
    padding: 40px 0;
    width: 280px;
    height: 180px;
    background: rgba(255, 255, 255, 1);
    border-radius: 15px;
    text-align: center;
    h2 {
      margin-top: 10px;
      margin-bottom: 44px;
      font-size: 15px;
      font-weight: 500;
      color: rgba(61, 61, 61, 1);
      line-height: 20px;
    }
    div {
      margin: 0 auto;
      width: 170px;
      height: 40px;
      background: rgba(28, 118, 254, 1);
      box-shadow: 0px 12px 30px 0px rgba(28, 118, 254, 0.15);
      border-radius: 6px;
      font-size: 15px;
      font-weight: 500;
      color: rgba(255, 255, 255, 1);
      line-height: 40px;
    }
  }
  .ReservationServicePopupAmend {
    padding: 25px;
    width: 230px;
    background: rgba(255, 255, 255, 1);
    border-radius: 15px;
    h2 {
      text-align: center;
      font-size: 16px;
      font-weight: 500;
      color: rgba(61, 61, 61, 1);
      line-height: 20px;
      margin-top: 10px;
      margin-bottom: 26px;
    }
    .van-radio {
      margin: 10px 0;
      padding-left: 19px;
      .van-radio__label {
        font-size: 14px;
        font-weight: 500;
        color: rgba(61, 61, 61, 1);
        line-height: 20px;
      }
      .van-radio__icon {
        font-size: 14px;
      }
    }
    .van-cell {
      width: 105px;
      height: 36px;
      background: rgba(249, 249, 250, 1);
      border-radius: 4px;
      line-height: 36px;
      padding: 0 10px;
    }
    .ReservationServicePopupAmendFrame {
      display: flex;
      justify-content: space-between;
    }
    .van-field__label {
      width: 35px;
      padding-right: 6px;
      font-size: 12px;
      font-weight: 500;
      color: rgba(61, 61, 61, 1);
    }
    .van-field__control {
      font-size: 12px;
    }
    select {
      padding: 0 10px;
      width: 105px;
      height: 36px;
      background: rgba(249, 249, 250, 1);
      border-radius: 4px;
      font-size: 12px;
      font-weight: 500;
      color: rgba(189, 189, 189, 1);

      border: none;
      appearance: none;
      -webkit-appearance: none; /*去除chrome浏览器的默认下拉图片*/
      -moz-appearance: none; /*去除Firefox浏览器的默认下拉图片*/
    }
    textarea {
      padding: 10px 2.67%;
      resize: none;
      width: 94.66%;
      height: 44px;
      background: rgba(249, 249, 250, 1);
      border-radius: 4px;
      border: none;
      font-size: 12px;
      font-weight: 500;
      // color: rgba(189, 189, 189, 1);
      line-height: 17px;
    }
    .ReservationServicePopupAmendButtom {
      margin-top: 27px;
      display: flex;
      justify-content: space-between;
      text-align: center;
      .ReservationServicePopupAmendButtomLeft {
        width: 37.86%;
        height: 40px;
        background: rgba(229, 239, 255, 1);
        border-radius: 6px;
        font-size: 15px;
        font-weight: 500;
        color: rgba(28, 118, 254, 1);
        line-height: 40px;
      }
      .ReservationServicePopupAmendButtomRight {
        width: 37.86%;
        height: 40px;
        background: rgba(28, 118, 254, 1);
        box-shadow: 0px 12px 30px 0px rgba(28, 118, 254, 0.15);
        border-radius: 6px;
        font-size: 15px;
        font-weight: 500;
        color: rgba(255, 255, 255, 1);
        line-height: 40px;
      }
    }
  }
}
</style>
